<template>
  <div class="youtube-responsive" :style="styleObject">
    <div class="aspect">
      <div class="content">
        <iframe :title="vidTittle" v-if="youtubeEmbed" width="100%" height="100%" :src="'https://www.youtube.com/embed/'+youtubeEmbed" frameborder="0" allowfullscreen></iframe>
        <video :title="vidTittle" v-if="videoEmbed" width="100%" height="100%" controls>
          <source :src="videoPath" type="video/mp4">
        </video>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    youtubeEmbed: {
      type: String,      
      required: false
    },
    videoEmbed: {
      type: String,      
      required: false
    },
    videoSize: {
      Type: Number,
      required: false
    },
    vidTittle: {
      type: String,
      required: true
    },
  },
  data(){
    return {
      videoPath: this.videoEmbed?require('../../assets/videos/'+this.videoEmbed):'',
      styleObject: {
        maxWidth: this.videoSize + "px",
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../sass/helpers/mixins";
.youtube-responsive {
  margin: auto;
  position: relative;
  max-width: 800px;
  width: 100%;
  .aspect {
    @include aspect-ratio(16, 9);
  }
}
</style>



