<template>
  <div id="app">
    <main>
      <section aria-label="contenido">
        <v-layout justify-center wrap>
          <v-flex xs12 sm12 md12 lg10>
            <router-view></router-view>
          </v-flex>
        </v-layout>
      </section>
    </main>
  </div>
</template>

<script>
export default {
  name: "app",
  computed: {
    color() {
      return this.$store.state.color;
    },
    linksPath() {
      return this.$store.state.linksPath;
    }
  }
};
</script>

<style src="./sass/styles.scss" lang="scss">

</style>



