<template>
  <v-layout row wrap justify-center>
    
    <!-- Imagenes -->
    <v-flex xs12>
      <h3>Imágenes</h3>
    </v-flex>
    <v-flex xs6 text-xs-center>
      <!-- <image-app :imaSize="200" imaSrc="logos-icons/mis-finanzas" imaAlt="Mis finanzas en casa"></image-app> -->
    </v-flex>
    <v-flex xs6 text-xs-center>
      <!-- En las imágenes que no tengan necesidad de ser verbalizadas se deja el imaAlt vacio -->
      <!-- <image-app :imaSize="180" imaSrc="logos-icons/bullet-ok-roja" imaAlt=""></image-app> -->
    </v-flex>

    <!-- Videos -->
    <v-layout row wrap>
      <v-flex xs12>
        <br><br><br>
        <h3>Videos</h3>
      </v-flex>
      <!-- el tamaño del video se modifica con el valor que se ponga en videoSize 
      Siempre poner un nombre para el video con el prop vidTittle-->
      <v-flex xs12 md6 text-xs-center>
        <h4>Video youtube</h4>
        <!-- para video de youtube se usa el prop youtubeEmbed
        el embed se toma desde la página de youtube, dando clik en compartir,  luego en insertar, y del url que da, se toma 
        lo que sale despues del embed/, por ejemplo de https://www.youtube.com/embed/TSuXenPuOgQ se toma TSuXenPuOgQ -->
        <video-app vidTittle="Lenguaje de señas" :videoSize="500" youtubeEmbed="TSuXenPuOgQ"></video-app>
      </v-flex>
      <v-flex xs12 md6 text-xs-center>
        <h4>Video local</h4>
        <!-- Para video local se pone el nombre y la extensión del video, todos los videos deben estar en la carpeta assets/videos -->
        <video-app vidTittle="Animación" :videoSize="500" videoEmbed="bunny.mp4"></video-app>
      </v-flex>
    </v-layout>

    <!-- Modals -->

    <v-dialog v-model="dialogOne" max-width="290">
      <v-card>
        <v-card-title class="headline">Titulo</v-card-title>
        <v-card-text>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Maecenas vestibulum felis condimentum fringilla tempor. Ut sagittis elementum diam, eget ultrices lacus. Nullam venenatis
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat="flat" @click.native="dialogOne = false">cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </v-layout>
</template>

<script>
export default {
  data() {
    return {
      contenidoUno: 0,
      contenidoDos: 0,
      dialogOne: false,
      lista: [
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Ut ",
        "non dolor dignissim, venenatis ",
        "sem ut, pharetra purus. Donec malesuada tortor vitae libero ",
        "luctus tristique. Aliquam molestie faucibus gravida. Aliquam "
      ],      
    };
  },
  methods: {
    currentBotOne(event) {
      this.contenidoUno = event;
    },
    currentBotTwo(event) {
      this.contenidoDos = event;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../sass/helpers/variables.scss";
</style>



