import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    color: "light",
    nextPant: false,
    screens: [0, 0],
    video: null,
    tema: '',
    linksPath: '/comics/',
    cantPagsMods: {moduloUno: 0},
    datosFormulario: []
  },
  mutations: {
    changeColor: state => state.color === "light" ? state.color = "dark" : state.color = "light",
    nextShow: (state, event) => state.nextPant = event,
    screenTrack: (state, [currentScr, cantScr]) => state.screens = [currentScr, cantScr],
    temaName: (state, event) => state.tema = event,
    guardarDato: (state, [pregNum, pregRes]) => state.datosFormulario[pregNum] = pregRes,
  }
});



