<template>
  <div class="vineta animated rotateInUpLeft">
    <image-app class="animated fadeInLeft" :imaSrc="imagePerso" :imaSize="200" imaAlt=""></image-app>
  </div>
</template>
<script>
export default {
  props: {
    imagePerso: {
      type: String,
      required: true
    }
  }
}
</script>


<style>
.vineta {
  overflow: hidden;
  background-image: url('../../assets/backgrounds/vineta-bor.png');
  background-size: contain;
  width: 220px;
  height: 290px;
  display: inline-block;
}
</style>



