<template>
  <v-layout row wrap justify-center :class="color">
    <router-link tag="button" class="iniciar-comic animated bounceIn" :to="linksPath+'1'" aria-label="Iniciar">
      Ver comic
    </router-link>
  </v-layout>
</template>

<script>
export default {
  data() {
    return {
      secuencia: []
    };
  },
  computed: {
    color() {
      return this.$store.state.color;
    },
    linksPath() {
      return this.$store.state.linksPath;
    }
  }
};
</script>

<style lang="scss" scoped>
@import '../sass/helpers/mixins.scss';
.iniciar-comic{
  margin-top: 50px;
  background-color: white;
  font-size: 20px;
  font-weight: bold;
  border-radius: 5px;
  padding: 10px 20px;
  @include shadow( 0 0 5px black);
}
</style>




