// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import ('../node_modules/vuetify/dist/vuetify.min.css')
import Vue from 'vue'
import Vuetify from 'vuetify'
import App from './App'
import router from './js/router'
import {
  store
} from './js/store/store'

import Imageimport from './components/ui/Imageimport'
import VideoImport from './components/ui/Videoimport'
import VinetaApp from './components/ui/ui-vineta'
import ActionBot from './components/ui/ul-action-bot'


Vue.config.productionTip = false

// This is the UI componentlibrary
Vue.use(Vuetify)

// Register here all the components
Vue.component('image-app', Imageimport)
Vue.component("video-app", VideoImport);
Vue.component("ui-vineta", VinetaApp);
Vue.component("ui-actionbot", ActionBot);


Vue.prototype.$color = 'light';
/* eslint-disable no-new */
new Vue({
  store: store,
  el: '#app',
  router,
  template: '<App/>',
  components: {
    App
  }
});



