import Vue from "vue"
import Router from "vue-router"
import Inicio from "@/components/Inicio"
import comicsIn from "@/components/screens/comics"
import Styleguide from "@/components/ui/Styleguide"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      name: "inicio",
      component: Inicio
    },
    {
      path: "/comics/:unipage",
      component: comicsIn
    },
    {
      path: "/styleguide",
      component: Styleguide
    }
  ]
})



