<template>
  <div class="act-bot hvr-grow-rotate" :class="backImg"></div>
</template>

<script>
export default {
  props:{
    backImg: {
      type: String,
      required: true
    }
  },
  data(){
    return {
      styleObject: {
        backgroundImage: 'url("../../assets/backgrounds/marco.png")',
      }
    }
  }
}
</script>


<style lang="scss" scoped>
@import '../../sass/helpers/mixins.scss';
@import '../../sass/helpers/variables.scss';
.act-bot{
  background-color: #DADADA;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  display: inline-block;
  margin: 10px;
  background-size: contain;
  @include shadow (0 0 10px black);
  &.botella{
    background-image: url('../../assets/buttons-icons/bot-botella.png')
  }
  &.porro{
    background-image: url('../../assets/buttons-icons/bot-porro.png')
  }
  &.reiniciar{
    background-image: url('../../assets/buttons-icons/reiniciar-bot.png')
  }
}
</style>



