<template>
  <div class="comic-container">
    <v-layout row wrap text-xs-center>
      <v-flex xs12>
        <image-app :imaSize="300" imaSrc="comic-01/comic-01-nombre" imaAlt=""></image-app>
      </v-flex>
      <v-flex xs12>
        <template v-if="!finComic" v-for="(bot, index) in botones">
          <ui-actionbot @click.native="clickSel(index, bot)" :backImg="bot"></ui-actionbot>
        </template>
        <div v-if="finComic"  class="animated rotateIn">
          <ui-actionbot @click.native="reiniciar()" backImg="reiniciar"></ui-actionbot>
        </div>
      </v-flex>
    </v-layout>
    <v-layout row wrap justify-center text-xs-center>
      <v-flex xs12 sm10 md8>
        <ui-vineta v-for="(item, index) in secuencia" :key="'secuencia-perso'+index" :imagePerso="item"></ui-vineta>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
export default {
  data() {
    return {
      botClicks: 0,
      finComic: false,
      secuenciaSeleccion: [],
      secuencia: ["comic-images/comic_01-02"],
      botones: ["botella", "porro"],
      eventosSec: [
        {
          selecciones: ["botella", "botella", "botella", "botella"],
          imagenes: [
            "comic-images/comic_01-03",
            "comic-images/comic_01-04",
            "comic-images/comic_01-05",
            "comic-images/comic_01-06"
          ]
        },
        {
          selecciones: ["porro", "porro", "porro", "porro"],
          imagenes: [
            "comic-images/comic_01-07",
            "comic-images/comic_01-08",
            "comic-images/comic_01-09",
            "comic-images/comic_01-10"
          ]
        },
        {
          selecciones: ["botella", "botella", "porro"],
          imagenes: [
            "comic-images/comic_01-03",
            "comic-images/comic_01-04",
            "comic-images/comic_01-11"
          ]
        },
        {
          selecciones: ["botella", "porro"],
          imagenes: ["comic-images/comic_01-03", "comic-images/comic_01-12"]
        },
        {
          selecciones: ["porro", "botella", "botella", "botella"],
          imagenes: [
            "comic-images/comic_01-07",
            "comic-images/comic_01-13",
            "comic-images/comic_01-05",
            "comic-images/comic_01-06"
          ]
        },
        {
          selecciones: ["porro", "botella", "porro", "botella"],
          imagenes: [
            "comic-images/comic_01-07",
            "comic-images/comic_01-13",
            "comic-images/comic_01-14",
            "comic-images/comic_01-15"
          ]
        },
        {
          selecciones: ["porro", "botella", "porro", "porro"],
          imagenes: [
            "comic-images/comic_01-07",
            "comic-images/comic_01-13",
            "comic-images/comic_01-14",
            "comic-images/comic_01-16"
          ]
        },
        {
          selecciones: ["porro", "porro", "botella"],
          imagenes: [
            "comic-images/comic_01-07",
            "comic-images/comic_01-08",
            "comic-images/comic_01-14"
          ]
        }
      ]
    };
  },
  methods: {
    clickSel(event, arr) {
      this.botClicks++;
      var botClicks = this.botClicks;
      var noEncontrado = 0;
      this.secuenciaSeleccion.push(arr);

      var secuenciaSeleccion = this.secuenciaSeleccion;

      for (var i = 0; i < this.eventosSec.length; i++) {
        var seleccionesComp = this.eventosSec[i].selecciones.slice(
          0,
          botClicks
        );
        if (secuenciaSeleccion.toString() === seleccionesComp.toString()) {
          this.secuencia.push(this.eventosSec[i].imagenes[botClicks - 1]);
          if (this.eventosSec[i].imagenes.length === botClicks) {
            this.finComic = true;
          }
          break;
        } else {
          noEncontrado++;      
        }

        if(noEncontrado === this.eventosSec.length){
          this.secuenciaSeleccion.pop();
          this.botClicks--;
        }
      }
    },
    reiniciar() {
      this.secuencia = ["comic-images/comic_01-02"];
      this.botClicks = 0;
      this.finComic = false;
      this.secuenciaSeleccion = [];
    }
  },
  computed: {
    currentPage() {
      return Math.floor(this.$route.params.unipage);
    }
  }
};
</script>




